.how-work{
    background-color: #3f3f3f;
    padding: 50px 0;
}
.featured-products{
    background-color: $black;
    &__product{
        &--txt{
            padding: 40px 0;
        }
    }
}
.why-choose{
    &__thumbs{
        &--icon{
            width: 85px;
        }
    }
}
.featured-quality{
    background-color: $dark-grey;
    &__block{
        &--txt{
            padding: 60px 40px;
        }
    }
}
.reserve{
    background-color: $black;
    padding: 60px 0;
}
.partner{
    background-color: $blue-grey;
    padding: 60px 0;
    &__thumb{
        &--img{
            width: 180px;
        }
    }
}
