button{
    background: none;
    border: none;
    cursor: pointer;
}
video, img{
    width: 100%;
}
a{
    color: inherit;
    &:hover{
        text-decoration: none;
    }
}
.btn{
    font-weight: bold;
    font-size: 0.7rem;
    color: #fff;
    border: 1px solid #fff;
    padding: 6px 35px;
    border-radius: 50px;
    text-transform: uppercase;

    transition: .3s ease-in-out;
    &:hover{
        color: $black;
        background-color: $white;
        border-color: $black;
    }
    &__grey{
        background-color: $blue-grey;
        border-color: $blue-grey;
    }
}
.warrper{
    max-width: 1140px;
    padding: 0 20px;
    margin: 0 auto;
}

/*------ Font ------*/

.txt{
    /*color*/
    &--white{
        color: $white;
    }
    &--blue-grey{
        color: $blue-grey;
    }
    &--grey{
        color: $grey;
    }
    &--black{
        color: $black;
    }
    /*size*/

    /*position*/
    &--center{
        text-align: center;
    }
    /*weight*/

    /*uppercase*/
    &--upper{
        text-transform: uppercase;
    }
}

