.menu{
    background: rgba(33, 33, 33, 0.98);
    position: fixed;
    width: 100%;
    &__warrper{

    }
    &__mobile-bar{
        display: flex;
        align-items: center;
        justify-content: space-between;

        padding: 20px 0;
        &--logo{
            width: 120px;
        }
    }
    &__links{
        padding: 20px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        &--link{
            color: #fff;
            font-size: 0.8rem;
            font-weight: bold;
            margin-bottom: 20px;
            transition: .18s ease-in-out;
            &:hover{
                color: $blue-grey;
            }
        }
        &--btn{
            width: 100%;
            padding-left: 0;
            padding-right: 0;

            text-align: center;

            border-color: transparent;
            &:hover{
                color: $blue-grey;
                border-color: $blue-grey;
                background-color: transparent;
            }
        }
    }
    &__burger{
        height: 15px;
        width: 15px;
        position: relative;
        .bar{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);

            height: 3px;
            width: 100%;
            background-color: #fff;
        }
        .bar:nth-of-type(1){
            top: 20%;
            transform: translate(-50%,-50%);
        }
        .bar:nth-of-type(3){
            top: 80%;
            transform: translate(-50%,-50%);
        }
    }
}
